+component(page-navigation)

  margin-top: 1em
  margin-bottom: 1em
  padding: 0.5em
  +border-radius(3px)

  +shadow-box

  color: $color-light-grey
  i
    color: $color-super-light-grey
    
  +part(left)

  +part(middle)
    +breakpoint('mobile')
      display: none

    +centered

  +part(right)
    float: right
